$arrowBgDefault: #fff;
$arrowBgHover: #4c4c4c;
$arrowShadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
$arrowSize: 48px;
$arrowIcon: url('../../../assets/images/common/slider-arrow.svg');

.slick-carousel {
  display: block;

  .slick-slider {
    padding: 0 60px;
    
    img {
      width: auto;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .slick-arrow {
    box-shadow: $arrowShadow;
    background-color: $arrowBgDefault;
    height: $arrowSize;
    width: $arrowSize;
    transform: unset;
    border-radius: 50%;
    color: transparent;
    z-index: 100;
    transition: background-color 0.3s ease-in-out;
    top: 0;
    bottom: 0;
    margin-top: auto;
    margin-bottom: auto;

    &::before {
      display: block;
      color: transparent;
      background-color: transparent;
      background-image: $arrowIcon;
      background-repeat: no-repeat;
      background-position: center center;
    }

    &:hover {
      background-color: $arrowBgHover;
    }

    &.slick-prev::before {
      transform: rotate(180deg);
    }
  }

  .slick-prev { left: 0; }
  .slick-next { right: 0; }
}