.rewards-container .guides {
  width: 100%;
  margin: 0 auto;
  font-size: 14px;
  font-weight: 600;
  color: #6E7F95; }
  .rewards-container .guides .gift-box-image {
    display: block;
    max-width: 100%;
    width: 250px;
    margin: 0 auto 30px auto; }
    @media screen and (max-width: 767px) {
      .rewards-container .guides .gift-box-image {
        display: none; } }
  .rewards-container .guides .indicators {
    display: block;
    position: relative;
    margin: 0 auto; }
    @media screen and (max-width: 767px) {
      .rewards-container .guides .indicators {
        display: flex;
        flex-direction: row;
        margin: 0 auto 40px auto; } }
    @media screen and (max-width: 375px) {
      .rewards-container .guides .indicators {
        flex-direction: column; } }
  .rewards-container .guides .indicator {
    outline: none;
    display: block;
    max-width: 100%;
    margin: 0 auto 40px auto;
    padding: 10px 24px;
    border-radius: 50px;
    width: 216px;
    background-color: transparent;
    font-size: 18px;
    font-weight: bold;
    color: #2C3652;
    transition: all 0.3s ease-in-out; }
    @media screen and (max-width: 767px) {
      .rewards-container .guides .indicator {
        display: inline-block;
        width: 50%;
        margin: unset;
        background-color: rgba(224, 234, 254, 0.2); } }
    @media screen and (max-width: 425px) {
      .rewards-container .guides .indicator {
        font-size: 14px; } }
    @media screen and (max-width: 375px) {
      .rewards-container .guides .indicator {
        width: unset;
        padding: 18px 24px;
        margin-bottom: 1rem; } }
    .rewards-container .guides .indicator:hover {
      background-color: rgba(224, 234, 254, 0.3); }
    .rewards-container .guides .indicator:focus {
      outline: none; }
    .rewards-container .guides .indicator.active {
      background-color: #E0EAFE; }
  .rewards-container .guides .carousel-item {
    display: block;
    opacity: 0;
    transition: visibility 0.6s ease-in-out, opacity 0.6s ease-in-out;
    transform: unset;
    visibility: collapse; }
    .rewards-container .guides .carousel-item.active {
      opacity: 1;
      visibility: visible; }
  .rewards-container .guides .offset-insurance-policies p {
    padding-right: 50px; }
    @media screen and (max-width: 767px) {
      .rewards-container .guides .offset-insurance-policies p {
        padding-right: unset; } }
  .rewards-container .guides .rewards-marketplace .item {
    display: block;
    width: 100%; }
  .rewards-container .guides .rewards-marketplace .image {
    width: 100%;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: left center; }
    @media screen and (max-width: 575px) {
      .rewards-container .guides .rewards-marketplace .image {
        display: none; } }
  .rewards-container .guides .rewards-marketplace .title {
    font-weight: bold;
    font-size: 18px;
    text-align: center;
    margin: 1rem 0;
    padding-right: 31.87%;
    color: #2C3652; }
    @media screen and (max-width: 575px) {
      .rewards-container .guides .rewards-marketplace .title {
        padding-right: unset; } }
  .rewards-container .guides .rewards-marketplace .desc {
    text-align: left;
    padding-right: 31.87%; }
    @media screen and (max-width: 575px) {
      .rewards-container .guides .rewards-marketplace .desc {
        padding-right: unset; } }
