.rewards-container .welcome {
  padding-top: 130px;
  padding-bottom: 30px; }
  @media screen and (max-width: 767px) {
    .rewards-container .welcome {
      padding-top: 50px; } }
  .rewards-container .welcome__title {
    max-width: 332px; }
    @media screen and (max-width: 767px) {
      .rewards-container .welcome__title {
        max-width: unset; } }
  .rewards-container .welcome__sub-title {
    max-width: 332px;
    font-size: 14px;
    margin-bottom: 25px;
    color: #6E7F95; }
    @media screen and (max-width: 767px) {
      .rewards-container .welcome__sub-title {
        max-width: unset; } }
  .rewards-container .welcome__main-img {
    max-width: 100%; }
  .rewards-container .welcome__partner-title {
    font-size: 12px;
    margin-bottom: 10px;
    color: #B6BDCB; }
  .rewards-container .welcome .slick-carousel {
    max-width: 450px; }
    @media screen and (max-width: 767px) {
      .rewards-container .welcome .slick-carousel {
        max-width: unset; } }
    .rewards-container .welcome .slick-carousel .slick-slide {
      padding-left: 5px !important;
      padding-right: 5px !important; }
      .rewards-container .welcome .slick-carousel .slick-slide img {
        max-width: 100%;
        width: 87.5px; }
