.rewards-container .form-landing-information-container .main-title {
  font-size: 7.313rem; }
  @media screen and (max-width: 1090px) {
    .rewards-container .form-landing-information-container .main-title {
      font-size: 7rem; } }

.rewards-container .credits {
  width: 100%;
  margin: 0 auto; }
  .rewards-container .credits .item {
    padding: 0 0.5rem; }
    .rewards-container .credits .item .image {
      display: flex;
      margin-left: auto;
      margin-right: auto;
      box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
      background-color: #fff;
      border-radius: 50%;
      width: 160px;
      height: 160px;
      padding: 40px;
      text-align: center;
      justify-content: center; }
      @media screen and (max-width: 992px) {
        .rewards-container .credits .item .image {
          width: 120px;
          height: 120px;
          padding: 35px; } }
      .rewards-container .credits .item .image > img {
        max-width: 100%;
        max-height: 100%; }
    .rewards-container .credits .item .title {
      color: #2C3652;
      font-size: 18px;
      margin: 20px auto;
      text-align: center;
      font-weight: bold; }
    .rewards-container .credits .item .desc {
      color: #6E7F95;
      font-size: 14px;
      font-weight: 600;
      max-width: 200px;
      text-align: center;
      margin: 0 auto; }

.rewards-container .blogs {
  width: 100%;
  margin: 0 auto; }
  .rewards-container .blogs .item {
    padding: 0 0.5rem; }
    .rewards-container .blogs .item .image {
      display: block;
      margin-left: auto;
      margin-right: auto;
      box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
      background-color: #fff;
      border-radius: 50%;
      max-width: 100%;
      width: 100%;
      padding-top: 60%;
      border-radius: 10px;
      cursor: pointer; }
    .rewards-container .blogs .item .title {
      color: #2C3652;
      font-size: 18px;
      margin-top: 11px;
      margin-bottom: 6px;
      text-align: left;
      font-weight: bold;
      cursor: pointer; }
    .rewards-container .blogs .item .desc {
      color: #6E7F95;
      font-size: 14px;
      font-weight: 600;
      text-align: left; }

.rewards-container .where-to-check {
  width: 100%;
  margin: 0 auto; }
  .rewards-container .where-to-check .image {
    max-width: 100%;
    margin-left: auto;
    margin-right: auto; }
    @media screen and (max-width: 767px) {
      .rewards-container .where-to-check .image {
        margin-bottom: 1.25rem; } }
  .rewards-container .where-to-check .title {
    color: #2C3652;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 6px; }
  .rewards-container .where-to-check .desc {
    color: #6E7F95;
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 50px; }
  .rewards-container .where-to-check .info {
    display: block;
    max-width: 300px;
    margin-right: 0;
    margin-left: auto; }
    @media screen and (max-width: 767px) {
      .rewards-container .where-to-check .info {
        max-width: unset;
        margin-right: unset;
        margin-left: unset; } }

.rewards-container .tnc .card-body > ol {
  color: #2C3652;
  font-weight: bold; }

.rewards-container .tnc .image {
  max-width: 100%;
  width: 700px; }

.rewards-container .tnc .term-block {
  margin-bottom: 1rem; }
  .rewards-container .tnc .term-block .title {
    font-size: 16px;
    font-weight: bold;
    color: #2C3652; }
  .rewards-container .tnc .term-block .details-list {
    list-style: square;
    color: #6E7F95;
    font-size: 14px;
    font-weight: 600; }
